import React from "react";
import PropTypes from "prop-types";

import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";

class BalanceState extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};

    this.render = this.render.bind(this);
  }

  render() {
    const { contractBalance, userBalance, className } = this.props;

    return (
      <Col className={`d-flex flex-column fs-4 ${className}`}>
        <Row>
          <Col xs="8">
            <Row>
              Contract Balance:&nbsp;
            </Row>
            <Row>
              Total Miners:&nbsp;
            </Row>
          </Col>
          <Col className="fw-bold">
            <Row>
              {contractBalance}
            </Row>
            <Row>
              {userBalance}
            </Row>
          </Col>
        </Row>
      </Col>
    );
  }
}

BalanceState.propTypes = {
  className: PropTypes.string,
  contractBalance: PropTypes.number,
  userBalance: PropTypes.number,
};

BalanceState.defaultProps = {
  className: PropTypes.string,
  contractBalance: 0,
  userBalance: 0,
};

export default BalanceState;
