import React from "react";

import ReactDOM from "react-dom";
import {
  Switch, HashRouter as Router, Route, useLocation,
} from "react-router-dom";

import "./index.css";

import App from "./views/App/App";
import Faq from "./views/Faq/Faq";

function ScrollToTop() {
  const { pathname } = useLocation();

  React.useEffect(() => {
    window.scrollTo(0, 0);
  }, [pathname]);

  return null;
}

ReactDOM.render(
  <React.StrictMode>
    <Router>
      <ScrollToTop />
      <Switch>
        <Route path="/faq">
          <Faq />
        </Route>
        <Route path="/">
          <App />
        </Route>
      </Switch>
    </Router>
  </React.StrictMode>,
  document.getElementById("root"),
);
