import React from "react";
import PropTypes from "prop-types";

import Badge from "react-bootstrap/Badge";
import Button from "react-bootstrap/Button";
import Col from "react-bootstrap/Col";
import Form from "react-bootstrap/Form";
import Row from "react-bootstrap/Row";

import "./HireMinersDialog.scss";

class HireMinersDialog extends React.Component {
  constructor(props) {
    super(props);
    this.state = { minersCount: 0, bnbAmount: 0 };

    this.render = this.render.bind(this);
    this.inputBnbAmount = this.inputBnbAmount.bind(this);
  }

  inputBnbAmount(event) {
    const value = Number(event.target.value);
    if (value >= 0 && value === parseInt(event.target.value, 10)) {
      this.setState({ bnbAmount: value });
    }
  }

  render() {
    const { userBalance } = this.props;
    const { minersCount, bnbAmount } = this.state;
    return (
      <Col>
        <Row>
          <Col xs="auto">
            Enter BNB Amount
          </Col>
        </Row>
        <Row className="align-items-center">
          <Col className="border rounded">
            <Row className="position-relative align-items-center">
              <Form.Control type="number" value={String(bnbAmount)} onChange={this.inputBnbAmount} className="bg-transparent border border-dark rounded HireMinersDialog__input" />
              <Badge className="fs-6 position-absolute bg-transparent text-dark HireMinersDialog__badge">BNB</Badge>
            </Row>
          </Col>
          <Col xs="auto" className="ps-4">
            <Button className="rounded rounded-pill bg-dark border HireMinersDialog__button">
              Hire&nbsp;
              {minersCount}
              &nbsp;Miners
            </Button>
          </Col>
        </Row>
        <Row>
          <Col xs="auto">
            Your Balance:&nbsp;
            {userBalance}
          </Col>
        </Row>
        <Row className="mt-4">
          <Col>
            <Button className="rounded rounded-pill bg-dark border HireMinersDialog__button">
              Hire More Miners
            </Button>
          </Col>
          <Col>
            <Button className="rounded rounded-pill bg-dark border HireMinersDialog__button">
              Pocket Your BNB
            </Button>
          </Col>
        </Row>
      </Col>
    );
  }
}

HireMinersDialog.propTypes = {
  userBalance: PropTypes.number,
};

HireMinersDialog.defaultProps = {
  userBalance: 0,
};

export default HireMinersDialog;
